import axios from 'axios';
import { API_KEY, URL_API, CITY_DATA } from './store/types';
import { useDispatch, useSelector } from 'react-redux';
import React from "react";
import {GetPriceProductBasketNumber, GetTypePriceProductBasket} from "./components/Basket/getPriceProductBasket";
import {NumberFormatPrice} from "./templateComponents/NumberFormat/NumberFormat";

export class PromokodeClass extends React.Component {

    constructor(props){
        super(props);
        //console.log(' ');
    }


    // static init() {
    //     return (()=>{
    //         console.log('   ');
    //         //this.getSlideHeader();
    //     });
    // }


    static getSalePromoOK(Product) {
        let go_sale_ok = true;
        // if (typeof PromoData == 'undefined') PromoData = {};

        //    .      .
        GetTypePriceProductBasket(Product) ? go_sale_ok = false : go_sale_ok = true;
       
        return go_sale_ok;
    }


    //   
    static getPriceFromPromokode(
        Product, //    (        
        SumReturn = 0, //    
        type_price, //    ( )
        countProductBasket = 0, //-     
        Type_Product, //  type_product 0,1,2,3  all
        PromoData = {}, //        
        countProductListBasket = 0, //-        
        idProduct = 0,
        return_default = false, // id    ,
        modifyList = [], //  ,
        sumProductDiscount = 0 //    ,    0,    
    ) {

       // console.log('     ',return_default);

        let countReturnNext = 1;
        let countProductinCartLocal = 0;

        let conditionProductPromokode = {
            "status":false,
            "count":0,
            "id":0,
            "type_id":0,
            "category_id":0
        };

        type_price = GetPriceProductBasketNumber(Product,countReturnNext,false, modifyList);
        let type_priceOld = GetPriceProductBasketNumber(Product,countReturnNext,false, modifyList,true);

        // console.log('  type_price = ',type_price);

        if(PromoData !== null){
            if(PromoData?.promokodeInfo?.addition_discount === true){
                return_default = false;
            }
        }


        if(return_default || PromoData === null){
            SumReturn = SumReturn + countProductBasket * type_price;
            // console.log('   ',return_default);
            return {
                "SumResult":parseFloat(SumReturn.toFixed(0)),
                "countProductListBasket":countProductListBasket,
                "sumProductDiscount":sumProductDiscount,
                "countProductinCartLocal":countProductinCartLocal,
                "conditionProductPromokode":conditionProductPromokode,
                "discountOneProduct":0
            };
        }else{
            // console.log('   ');
        }

        let sales = 0;
        let proc_Sum = 0;

        let go_sale_ok = true;

            switch (PromoData.promokodeInfo.sale_type) {
                case "0":
                    //  %.
                    sales = PromoData.promokodeInfo.sale / 100;
                    proc_Sum = NumberFormatPrice(type_priceOld * sales,0,'','');
                    break;
                case "1":
                    //  .
                    sales = PromoData.promokodeInfo.sale;
                    proc_Sum = sales;
                    if (proc_Sum < 0) {
                        proc_Sum = 0;
                    }
                    break;
                default:
                    console.log('    %  ');
                    //    -    .
                    sales = PromoData.promokodeInfo.sale;
                    proc_Sum = sales;
                    if (proc_Sum < 0) {
                        proc_Sum = 0;
                    }
            }

            console.log('     : sales = ',sales,'proc_Sum=',proc_Sum);


            if(!return_default) {
                if (PromoData?.promokodeInfo?.addition_discount === true) {
                   go_sale_ok = true;
                } else {
                    go_sale_ok = this.getSalePromoOK(Product);
                }
            }else {
                go_sale_ok = false;
            }


            //console.log(' type_price ',Product,countReturnNext,type_price);

//            console.log(type_price);

        // console.log('  Product',Product);

        //  
        if (PromoData.promokodeInfo.type === "sale_category") {
            // console.log('  ,  ');
            if (go_sale_ok === true) {
                //      (     )
                if (Product.categoryId === PromoData.promokodeInfo.id_category) {
                    //       ,  
                    if (countProductListBasket >= (PromoData.productInfo.count_product*1) && (PromoData.productInfo.count_product*1) !== 0) {
                        //-          -    (       ),  . 
                        SumReturn = SumReturn + countProductBasket * type_price;
                    } else {
                        //-        ,    ()
                        if (countProductBasket <= (PromoData.productInfo.count_product*1)) {
                            //-   ,    -    
                            if (Type_Product === PromoData.productInfo.type_product || PromoData.productInfo.type_product === "all") {
                                //       (       ),  
                                SumReturn = SumReturn + countProductBasket * NumberFormatPrice((type_priceOld - proc_Sum),0,'.','');
                                sumProductDiscount += countProductBasket * NumberFormatPrice((type_priceOld - proc_Sum),0,'','');
                                countProductinCartLocal += countProductBasket; //-    ,  
                                conditionProductPromokode = {
                                    "status":true,
                                    "count":countProductinCartLocal,
                                    "id":Product.id_Product,
                                    "type_id":Product.type_product,
                                    "category_id":Product.id_Category,
                                    "discountOneProduct":proc_Sum
                                } //    ,     
                            } else {
                                //        ,  . 
                                SumReturn = SumReturn + countProductBasket * type_price;
                            }
                        } else {
                            //-   ,  -    
                            if (Type_Product === PromoData.productInfo.type_product || PromoData.productInfo.type_product === "all") {
                                //       (       ),  
                                let difference = 0; //    - () 
                                difference = countProductBasket - (PromoData.productInfo.count_product*1);
                                //  -    
                                SumReturn = SumReturn + (PromoData.productInfo.count_product*1) * NumberFormatPrice((type_priceOld - proc_Sum),0,'','');
                                SumReturn = SumReturn + difference * type_price;
                                sumProductDiscount += (PromoData.productInfo.count_product*1) * NumberFormatPrice((type_priceOld - proc_Sum),0,'',''); //     ,     
                                countProductinCartLocal += (PromoData.productInfo.count_product*1); //-    ,  
                                //     .

                                conditionProductPromokode = {
                                    "status":true,
                                    "count":countProductinCartLocal,
                                    "id":Product.id_Product,
                                    "type_id":Product.type_product,
                                    "category_id":Product.id_Category,
                                    "discountOneProduct":proc_Sum
                                } //    ,     

                            } else {
                                //        ,  . 
                                SumReturn = SumReturn + countProductBasket * type_price;
                            }
                        }
                        // -   ,   ,             
                        countProductListBasket++;
                    }
                } else {
                    //id      id    ,   
                    SumReturn = SumReturn + countProductBasket * type_price;
                }
            } else {
                //   ,    ,          +
                SumReturn = SumReturn + countProductBasket * type_price;
            }
        }

        //  
        if (PromoData.promokodeInfo.type === "sale_product") {
            // console.log('    ');
            if (go_sale_ok === true) {
                //      (     )
                if (idProduct === PromoData.productInfo.id_product) {
                    //id    id    
                    if (countProductBasket <= (PromoData.productInfo.count_product*1) || (PromoData.productInfo.count_product*1) === 0) {
                        //-   ,    -    
                        if (Type_Product === PromoData.productInfo.type_product || PromoData.productInfo.type_product === "all") {
                            //       (       ),  
                            SumReturn = SumReturn + countProductBasket * NumberFormatPrice((type_priceOld - proc_Sum),0,'','');
                            sumProductDiscount += countProductBasket * NumberFormatPrice((type_priceOld - proc_Sum),0,'','');
                            countProductinCartLocal += countProductBasket; //-    ,  
                            conditionProductPromokode = {
                                "status":true,
                                "count":countProductinCartLocal,
                                "id":Product.id_Product,
                                "type_id":Product.type_product,
                                "category_id":Product.id_Category,
                                "discountOneProduct":proc_Sum
                            } //    ,     
                        } else {
                            //        ,  . 
                            SumReturn = SumReturn + countProductBasket * type_price;
                        }
                    } else {
                        //-   ,   -    
                        if (Type_Product === PromoData.productInfo.type_product || PromoData.productInfo.type_product === "all") {
                            //       (       ),  
                            let difference = 0; //    - () 
                            difference = countProductBasket - (PromoData.productInfo.count_product*1);
                            //  -    
                            SumReturn = SumReturn + (PromoData.productInfo.count_product*1) * NumberFormatPrice((type_priceOld - proc_Sum),0,'','');
                            SumReturn = SumReturn + difference * type_price;
                            sumProductDiscount += (PromoData.productInfo.count_product*1) * NumberFormatPrice((type_priceOld - proc_Sum),0,'',''); //       
                            countProductinCartLocal += (PromoData.productInfo.count_product*1); //-    ,  
                            conditionProductPromokode = {
                                "status":true,
                                "count":countProductinCartLocal,
                                "id":Product.id_Product,
                                "type_id":Product.type_product,
                                "category_id":Product.id_Category,
                                "discountOneProduct":proc_Sum
                            } //    ,     
                            //     .
                        } else {
                            //        ,  . 
                            SumReturn = SumReturn + countProductBasket * type_price;
                        }
                    }
                } else {
                   // console.log('   , ',SumReturn, countProductBasket, type_price);
                    //id     id    
                    SumReturn = SumReturn + countProductBasket * type_price;
                }
                // -   ,   ,             
                countProductListBasket++;
            } else {
                //   ,    ,          +
                SumReturn = SumReturn + countProductBasket * type_price;
            }
        }
        //  
        if (PromoData.promokodeInfo.type === "sale_all") {
            //         
            if (go_sale_ok === true) {
                //      (     )
                if (countProductListBasket >= (PromoData.productInfo.count_product*1) && (PromoData.productInfo.count_product*1) !== 0) {
                    //-          -    (       ),  . 
                    SumReturn = SumReturn + countProductBasket * type_price;
                } else {
                    //-        ,    ()
                    if (countProductBasket <= (PromoData.productInfo.count_product*1) || (PromoData.productInfo.count_product*1) === 0) {
                        //-   ,    -    
                        if (Type_Product === PromoData.productInfo.type_product || PromoData.productInfo.type_product === "all") {
                            console.log('   2 - -    N',Type_Product+" === "+PromoData.productInfo.type_product );
                            //       (       ),  
                            SumReturn = SumReturn + countProductBasket * NumberFormatPrice((type_priceOld - proc_Sum),0,'','');
                            sumProductDiscount += countProductBasket * NumberFormatPrice((type_priceOld - proc_Sum),0,'','');
                            conditionProductPromokode = {
                                "status":true,
                                "count":countProductinCartLocal,
                                "id":Product.id_Product,
                                "type_id":Product.type_product,
                                "category_id":Product.id_Category,
                                "discountOneProduct":proc_Sum
                            } //    ,     
                            countProductinCartLocal += countProductBasket; //-    ,  

                            console.log('countProductinCartLocal =',countProductinCartLocal," countProductBasket=",countProductBasket );

                        } else {
                            //        ,  . 
                            console.log('    - -    1',Type_Product+" === "+PromoData.productInfo.type_product );
                            SumReturn = SumReturn + countProductBasket * type_price;
                        }
                    } else {
                        //-   ,   -    
                        if (Type_Product === PromoData.productInfo.type_product || PromoData.productInfo.type_product === "all") {
                            console.log('   3 - -    3',Type_Product+" === "+PromoData.productInfo.type_product );
                            //       (       ),  
                            let difference = 0; //    - () 
                            difference = countProductBasket - (PromoData.productInfo.count_product*1);
                            //  -    
                            SumReturn = SumReturn + (PromoData.productInfo.count_product*1) * NumberFormatPrice((type_priceOld - proc_Sum),0,'','');
                            SumReturn = SumReturn + difference * type_price;
                            sumProductDiscount += (PromoData.productInfo.count_product*1) * NumberFormatPrice((type_priceOld - proc_Sum),0,'',''); //       
                            countProductinCartLocal += (PromoData.productInfo.count_product*1); //-    ,  
                            conditionProductPromokode = {
                                "status":true,
                                "count":countProductinCartLocal,
                                "id":Product.id_Product,
                                "type_id":Product.type_product,
                                "category_id":Product.id_Category,
                                "discountOneProduct":proc_Sum
                            } //    ,     
                            //     .
                        } else {
                            console.log('   4 - -    1',Type_Product+" === "+PromoData.productInfo.type_product );
                            //        ,  . 
                            SumReturn = SumReturn + countProductBasket * type_price;
                        }
                    }
                }
                // -   ,   ,             
                countProductListBasket++;
            } else {
                //   ,    ,          +
                SumReturn = SumReturn + countProductBasket * type_price;
            }
        }


        return {
            "SumResult":parseFloat(SumReturn.toFixed(0)),
            "countProductListBasket":countProductListBasket,
            "sumProductDiscount":sumProductDiscount,
            "countProductinCartLocal":countProductinCartLocal,
            "conditionProductPromokode":conditionProductPromokode
        };
    }

    static checkProductAddToCart(){

    }
}